<template>
    <div class="Header">
        <div class="headerTop">
            <div class="content clearfix">
                <div class="left">
                    <span class="login">请登陆</span>
                    <span class="phone">产品热线：028-88888888</span>
                </div>
                <div class="right">
                    <a>收藏夹</a>|
                    <a>帮助</a>|
                    <a>注销</a>
                    <a>经销商助手</a>
                </div>

            </div>
        </div>
        <div class="headerBottom">
            <div class="content clearfix">
                <div class="logo"><img src="src/assets/logo.png" alt=""></div>
                <div class="nav">
                    <ul>
                        <template v-for="(item,index) in navList">
                            <router-link :to="item.routeTo" :key="index">
                                <li @click="headerClick(index)" :class="[item.hasSelected?'navItemSelected':'']">{{item.name}}</li>
                            </router-link>
                        </template>
                    </ul>
                </div>
                <div v-if="false" class="search">
                    <div class="inBill" @click="routeToPurchaseBills">
                        <span class="car">
                            <img src="data:image/svg+xml;base64," alt="">
                        </span>
                        <el-badge :value="purchaseCount" class="item">
                            <span class="text">进货单</span>
                        </el-badge>
                        <!-- <span class="text">进货单
                                            <i>{{purchaseCount}}</i>
                                        </span> -->
                    </div>
                    <div class="searchSection">
                        <div class="searchInp">
                            <!-- <input type="text" placeholder="搜索内容" name="" value=""> -->
                            <el-input placeholder="搜索内容" v-model="searchInfo" class="input-with-select">
                                <el-button @click="search" slot="append" icon="el-icon-search"></el-button>
                            </el-input>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations } from 'Vuex';
export default {
    name: 'Header',
    data() {
        return {
            searchInfo: "",
        }
    },
    methods: {
        ...mapMutations({
            changeCurrentNav: 'changeCurrentNav'
        }),
        headerClick(index) {
            this.changeCurrentNav({ index });
            
        },
        search() {
            console.log(this.searchInfo.trim());
        },
        routeToPurchaseBills() {
            this.$router.push({ name: 'PurchaseBills', params: {} });
            this.$store.commit('changeCurrentNav',{hash:''})
        }

    },
    computed: {
        ...mapState({
            purchaseCount(state) {
                return state.purchaseCount;
            },
            navList(state) {
                return state.navList
            }
        })
    },

}
</script>
<style lang="scss">
@import "./Header.scss";
</style>




